/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 648px) {
    #root .three-d-player-space {
        width: 100%;
    }
    #root .template-wrapper .next-btn{
        margin-top:30px
    }
    
}

@media screen and (max-width: 1024px) {
    #root .secondary-bar-wrapper {
        display: block;
        height: auto;
    }
    #root .three-d-player-space {
        margin: 0 auto;
    }
}
@media screen and (max-width: 768px) {
    #root .bHKlIe,
    #root .Omcxy {
        display: block;
        width: 100%;
        padding: 0 40px;
    }
    #player-root {
        width: auto;
        max-width: none;
        height: 400px;
    }

    #root .primary-bar-wrapper {
        height: auto;
    }
    
    #root .primary-bar-wrapper .primary-bar-item {
        font-size: 14px;
        max-height: 57px;
    }
    #root .secondary-bar-wrapper .secondary-bar-item {
        font-size: 14px;
        height: 26px;
        padding: 2px 10px;
        border-bottom: solid 2px #eee;
        -webkit-transform: none;
        -ms-transform: none;
        transform: none;
    }
    #root .section-wrapper {
        width: 100%;
    }
    #root .template-wrapper > div {
        margin: auto;
        width: 100%;
    }
    #root .player-container {
        padding: 0 20px;
        width: 100%;
    }
  }

